.popup {
  position: fixed;
  z-index: 9999;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  top: 0;
  background-color: @black-rgba-three;

  &.d-none {
    display: none;
  }

  &.remove {
    animation: popupWrapperHide 0.3s cubic-bezier(0.13, 0.3, 0.17, 0.99);

    & .popup__content {
      animation: popupHide 0.3s cubic-bezier(0.13, 0.3, 0.17, 0.99);
    }
  }

  &__content {
    position: absolute;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 60px 20px 20px;
    border-radius: 25px;
    background-color: @white;
    animation: popupShow 0.3s cubic-bezier(0.13, 0.3, 0.17, 0.99);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 28px 59px @black-rgba-two,
    0 40px 120px @black-rgba-five;

    @media only screen and (max-width: 319.98px) {
      max-width: 275px;
    }

    @media only screen and (max-width: 575px) and (max-height: 451px) {
      padding: 20px 20px 20px;
    }

    & .button {
      margin: 10px;
    }

    .button-close {
      padding: 0;
      position: absolute;
      display: block;
      z-index: 2;
      width: 50px;
      height: 50px;
      top: -15px;
      right: -15px;
      border-radius: 50%;
      border: none;
      background-color: @grey-two;
      background-image: none;
      box-sizing: border-box;
      transition: 0.5s;
      cursor: pointer;
      transform: rotate(0);

      @media (max-width: @tablet-width) {
        width: 40px;
        height: 40px;
      }

      @media only screen and (max-width: 450.98px) {
        top: -10px;
        right: -10px;
      }

      @media only screen and (max-width: 380px) {
        top: 5px;
        right: 5px;
        border-radius: 0;
        background-color: transparent;
      }

      @media only screen and (max-width: 575px) and (max-height: 451px) {
        top: -10px;
        right: -10px;
        border: none;
      }

      &:hover {
        background-color: @color-red;

        @media only screen and (min-width: 451px) {
          transform: rotate(180deg);
        }

        &::before {
          transform: translate(-50%, -50%) rotate(135deg);
        }

        &::after {
          transform: translate(-50%, -50%) rotate(-135deg);
        }
      }

      &:focus {
        background-color: @color-red;
      }

      &::after,
      &::before {
        content: "";
        position: absolute;
        display: block;
        width: 28px;
        height: 2px;
        top: 50%;
        left: 50%;
        background-color: @white;
        transition: 0.5s;
        border-radius: 30px;
        @media only screen and (max-width: 380px) {
          background-color: @color-green;
        }
      }

      &::before {
        transform: translate(-50%, -50%) rotate(45deg);
      }

      &::after {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }
  }
}

@keyframes popupShow {
  0% {
    opacity: 0;
    visibility: hidden;
    transform: translate(-50%, -50%) scale(1.8);
  }

  100% {
    visibility: visible;
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

@media only screen and (max-width: 1023.98px) {
  @keyframes popupShow {
    0% {
      opacity: 0;
      transform: translate(-50%, -100%);
    }

    100% {
      opacity: 1;
      transform: translate(-50%, -50%);
    }
  }
}

@keyframes popupHide {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, -50%) scale(1);
  }

  100% {
    opacity: 0;
    visibility: hidden;
    transform: translate(-50%, -50%) scale(1.8);
  }
}

@media only screen and (max-width: 1023.98px) {
  @keyframes popupHide {
    0% {
      opacity: 1;
      transform: translate(-50%, -50%);
    }

    100% {
      opacity: 0;
      transform: translate(-50%, -100%);
    }
  }
}


@keyframes popupWrapperShow {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes popupWrapperHide {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
