.grid {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 0;

  @media (min-width: @desktop-width) {
    padding: 0 40px;
  }

  &__item {
    width: 100%;
    max-width: 335px;
    padding: 0 10px;
    margin-bottom: 35px;
    box-sizing: border-box;

    @media (min-width: @desktop-width) {
      margin-bottom: 35px;
    }
  }
}
