.attach {
  &__wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }

  &__head {
    text-transform: uppercase;
    font-weight: 700;
    padding: 0 0 5px 0;
    margin: 0 0 5px 0;
  }

  &__item {
    display: flex;
    padding: 0;
    margin: 0 20%;
    width: 60%;
    justify-content: center;
    transition: .5s ease-in-out;

    @media screen and (max-width: 400px) {
      width: 100%;
      margin: 0;
    }

    &:last-of-type {
      margin-bottom: 0;
      border-bottom: none;
    }

    label {
      padding-bottom: 30px;
      transition: .3s ease-in-out;
    }
  }

  &__container-created-files {
    position: relative;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
  }

  &__wrapper-created-files {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: auto;
    margin: 0;
    padding-left: 0;
    list-style: none;
    line-height: 1.8em;
    @media screen and (max-width: 440.98px) {
      margin-right: -5px;
      margin-left: -5px;
    }
    @media screen and (min-width: 441px) {
      display: block;
      width: 100%;
    }
  }

  &__file {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    font-size: 14px;
    @media screen and (max-width: 440.98px) {
      position: relative;
      overflow: hidden;
      -ms-flex: 0 0 33.333333%;
      flex: 0 0 33.333333%;
      max-width: 33.333333%;
      padding-right: 5px;
      padding-left: 5px;
      margin-bottom: 10px;
      height: 53px;
      font-size: 10px;
      font-weight: 500;
      line-height: 12px;
      letter-spacing: -0.5px;
    }
    @media screen and (max-width: 340px) {
      -ms-flex: 0 0 50%;
      flex: 0 0 50%;
      max-width: 50%;
    }

    &:first-child {
      @media screen and (max-width: 991.98px) {
        margin-top: 2px;
      }
      @media screen and (max-width: 440.98px) {
        margin-top: 0;
      }
    }

    &:nth-last-child(2) {
      @media screen and (max-width: 440.98px) {
        margin-bottom: 15px;
      }
    }

    &_wrapper {
      @media screen and (max-width: 440.98px) {
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        width: 100%;
        padding-top: 5px;
        height: 100%;
        background: @grey-six;
        border-radius: 3px;
      }

      @media screen and (min-width: 441px) {
        position: relative;
        display: flex;
        flex-basis: 0;
        flex-grow: 1;
        min-width: 0;
        max-width: 100%;
        width: 100%;
      }

      @media screen and (min-width: 576px) {
        flex-basis: 0;
        flex-grow: 1;
        min-width: 0;
        max-width: 100%;
      }

      & p {
        display: inline-block;
        overflow: hidden;
        width: 30px;
        margin: 0 0 5px;
        padding: 4px 5px;
        font-size: 8px;
        text-align: center;
        text-transform: uppercase;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: normal;
        color: @white;
        @media screen and (max-width: 440.98px) {
          height: 14px;
          padding: 2.4px 3px;
          font-size: 8px;
        }
        @media screen and (min-width: 441px) {
          order: 2;
          width: 100%;
          max-width: 55px;
          margin: 0 10px 0 0;
          border-radius: 0 5px 5px 0;
          font-size: 13px;
        }
      }

      & .progress {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        @media screen and (max-width: 440.98px) {
          padding-right: 5px;
          padding-left: 5px;
        }
        @media screen and (min-width: 441px) {
          width: calc(100% - 10px);
        }

        &-bg {
          height: 100%;
          background: @white-rgba-four;
          @media screen and (max-width: 440.98px) {
            border-radius: 3px;
            background: @black-rgba-three;
          }

          & .progress-bar__mobile_svg {
            @media screen and (min-width: 441px) {
              display: none;
            }
          }
        }

        &-bar {
          background: fadeout(@blue-four, 60%);
          @media screen and (max-width: 440.98px) {
            display: none;
          }

          &:after {
            @media screen and (max-width: 440.98px) {
              display: none;
            }

            @media screen and (min-width: 441px) {
              animation: progress_barr 2.5s linear infinite;
              background: rgb(47, 128, 237); /* Old browsers */
              background: -moz-linear-gradient(left, rgba(47, 128, 237, 0.4) 0%, rgba(47, 128, 237, 0.4) 100%); /* FF3.6-15 */
              background: -webkit-linear-gradient(left, rgba(47, 128, 237, 0.4) 0%, rgba(47, 128, 237, 0.4) 100%); /* Chrome10-25,Safari5.1-6 */
              background: linear-gradient(to right, rgba(47, 128, 237, 0.4) 0%, rgba(47, 128, 237, 0.4) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
              filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00a1ff', endColorstr='#00a1ff',GradientType=1 ); /* IE6-9 */
              background-size: 100px;
              background-repeat: no-repeat;
            }
          }

          &.errorLoad {
            background: rgba(254, 79, 62, 0.6);

            &:after {
              @media screen and (max-width: 440.98px) {
                display: none;
              }
              @media screen and (min-width: 441px) {
                animation: progress_barr 2.5s linear infinite;
                background: rgb(254, 79, 62); /* Old browsers */
                background: -moz-linear-gradient(left, rgba(254, 79, 62, 0.4) 0%, rgba(254, 79, 62, 0.4) 100%); /* FF3.6-15 */
                background: -webkit-linear-gradient(left, rgba(254, 79, 62, 0.4) 0%, rgba(254, 79, 62, 0.4) 100%); /* Chrome10-25,Safari5.1-6 */
                background: linear-gradient(to right, rgba(254, 79, 62, 0.4) 0%, rgba(254, 79, 62, 0.4) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00a1ff', endColorstr='#00a1ff',GradientType=1 ); /* IE6-9 */
                background-size: 100px 40px;
                background-repeat: no-repeat;
              }
            }

            & ~ .progress-bar__mobile_svg {
              & .progress-bar__mobile_progress {
                @media screen and (max-width: 440.98px) {
                  stroke: @color-red;
                }
              }
            }
          }

          @keyframes progress_barr {
            0% {
              background-size: 100px 40px;
              background-position: -100px 0;
            }
            40% {
              background-size: 200px 40px;
            }
            90% {
              background-size: 100px 40px;
            }
            100% {
              background-position: 500px 0;
            }
          }
        }

        &-value {
          @media screen and (min-width: 441px) {
            right: 10px;
            left: auto;
            transform: translate(0, -50%);
            font-size: 13px;
            font-weight: 400;
            white-space: nowrap;
            text-shadow: none;
            color: @white;
          }
        }
      }
    }
  }

  &__button {
    @media screen and (max-width: 399.98px) {
      width: 100%;
    }
  }

  &_mt {
    margin-top: 10px;
  }

  &__up {
    cursor: pointer;
  }

  &__edit {
    display: none;
    margin-right: 10px;
    cursor: pointer;
    transition: .2s;

    a {
      text-decoration: none;

      &:hover {
        text-decoration: none;
      }
    }

    img {
      width: 20px;
      height: 20px;
      @media screen and (max-width: 600px) {
        width: 27px;
        height: 27px;
      }
    }

    &:hover {
      a {
        text-decoration: none;
      }
    }
  }

  &__delete {
    display: none;
    cursor: pointer;

    a {
      text-decoration: none;

      &:hover {
        text-decoration: none;
      }
    }

    img {
      width: 21px;
      height: 21px;
      @media screen and (max-width: 600px) {
        width: 27px;
        height: 27px;
      }
    }

    &:hover {
      a {
        text-decoration: none;
      }
    }
  }

  &__item--attached {
    &:nth-child(n+2) {
      padding-top: 20px;
    }

    .attach {
      &__up {
        display: none;
      }

      &__name {
        width: 60%;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        text-align: left;
        @media screen and (max-width: 600px) {
          line-height: 30px;
        }
      }

      &__edit {
        display: inline-block;
        margin-left: auto;
        margin-right: 30px;
        @media screen and (max-width: 339.98px) {
          margin-right: 0;
        }
      }

      &__delete {
        display: inline-block;
        margin-left: 20%;
      }

      &__button {
        @media screen and (max-width: 399.98px) {
          width: 0;
        }
      }
    }
  }

  &__input {
    display: none;
  }
}
